import React from "react";



export default class Project extends React.Component{
    constructor(props){
        super(props)
    }


    render(){
        return(
            <div className="column is-12-mobile is-half-tablet is-one-third-desktop">
            <figure className="image is-3by2"><img className="project-thumb" src={this.props.img} alt={"Screen du projet "  + this.props.title}/>
                <figcaption>
                <h3>{this.props.description}</h3>
                <h1 className="title is-size-5 is-size-4-widescreen">{this.props.title}</h1>
                <a className="button is-primary is-outlined is-rounded" href={this.props.uri} target="_blank" rel="noopener noreferrer">
                    <span>{this.props.title == "Sao Colmar" ? "View Website" : "View Github Code"}</span>
                    {this.props.title !== "Sao Colmar" ?
                        <span className="icon is-small">
                        <i className="fab fa-github"></i>
                        </span> : ""}
                </a>
                <p>Stacks used: {this.props.stack}</p>
                </figcaption>
                <div className="overlay"></div>
            </figure>
            </div>
        )
    }
}