import React from "react";

export default class Loader extends React.Component{
    constructor(){
        super();
    }
    componentDidMount(){
        const loader = document.querySelector(".main-loader");
        setTimeout(() => {
            this.fadeOut(loader)
        }, 1500);
    }

    fadeOut(element, timeout = 1000){
        let opacityToRemove = 1 / (timeout / 10);
        let realTimeout = timeout / 10;
        for(let i = 0;  i <= realTimeout; i++){
            setTimeout(() => {
                element.style.opacity = `${opacityToRemove * (realTimeout - i)}`
            }, i * 10)
        }
    }


    render(){
        return(
            <div className="main-loader">
                <div className="loader-holder">
                    <div className="loader">
                    </div>
                </div>
            </div>
        );
    }
}