import React from "react";
import Logo from "../Logo.png"


export default class Header extends React.Component{
    constructor(){
        super();
    }

    handleClick1(e){
        const sectionSkills = document.querySelector(".section.is-medium.is-primary.has-text-centered.is-long");
        sectionSkills.scrollIntoView();
    }

    handleClick2(){
        const sectionWorks = document.querySelector(".project-grid");
        sectionWorks.scrollIntoView();
    }

    handleBurger(){
        const burger = document.querySelector(".navbar-burger");
        const menu = document.querySelector(".navbar-menu");
        menu.classList.toggle("is-active")
        burger.classList.toggle("is-active");
    }

    render(){
        return(
            <nav className="navbar is-transparent" role="navigation" aria-label="main navigation">
                <div className="container">
                    <div className="navbar-brand">
                        <a className="navbar-item" href="/">
                            <img src={Logo} style={{borderRadius: "20px"}} alt="Aymen Hamada | Full Stack Developper &amp; Mentor" height="48"/>
                        </a>
                        <a className="navbar-burger" role="button" aria-label="menu" aria-expanded="false" data-target="#navMenu" onClick={this.handleBurger}>
                            <span aria-hidden="true"></span>
                            <span aria-hidden="true"></span>
                            <span aria-hidden="true"></span>
                        </a>
                    </div>
                    <div className="navbar-menu" id="navMenu">
                        <div className="navbar-start"></div>
                        <div className="navbar-item"><a className="button is-primary is-outlined is-rounded" onClick={this.handleClick1}>My journey</a>
                        <div className="navbar-item"><a className="button is-primary is-outlined is-rounded" onClick={this.handleClick2} >My project</a></div>
                        </div>
                    </div>
                </div>
            </nav>
        )
    }
}