import React from 'react';
import Header from "./components/Header"
import PresentationContent from "./components/PresentatonContent"
import MySkills from "./components/MySkills"
import Projects from "./components/Projects";
import Footer from "./components/Footer";
import Loader from "./components/Loader";

//Animations stuff

import styled, {keyframes} from "styled-components";
import { fadeIn } from "react-animations";

const FadeIn = styled.div`animation: 1s ${keyframes`${fadeIn}`}`


class App extends React.Component{
    constructor(){
        super();
        this.state = {videoLoaded: false    };
        this.handleVideoLoaded = this.handleVideoLoaded.bind(this);
    }


    handleVideoLoaded(){
        this.setState({videoLoaded: true});
    }


    render(){
        if(!this.state.videoLoaded){
            setTimeout(() => {
                this.handleVideoLoaded();
            }, 2000);
            return(
                <div>
                    <Loader/>
                </div>

            )
        }
        return (
            <FadeIn>
                <div className="App">
                    <Header/>
                    <PresentationContent/>
                    <MySkills/>
                    <Projects/>
                    <Footer/>
                </div>
            </FadeIn>
        )
  }
}

export default App;
